html {
  font-size: 6.25%; /*  1rem = 1px */
  box-sizing: border-box; /*hack para box mnodel*/
  scroll-snap-type: y mandatory;
}

*,
*:before,
*:after {
  box-sizing: inherit; /*hack para box mnodel*/
  margin: 0;
  padding: 0;
}

body {
  font-size: 15rem;
  font-family: "Inter", sans-serif;
  background-color: hsl(233deg, 47%, 7%);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1,
h2,
h3 {
  color: #000000;
}

h1 {
  font-size: 40rem;
}

h2 {
  font-size: 36rem;
}

h3 {
  font-size: 30rem;
}

p {
  font-size: 20rem;
  line-height: 1.5;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.contenedor {
  width: min(95%, 1200rem);
}

.contenedor-card {
  display: grid;
  place-items: center;
}

.card {
  max-width: 400rem;
  text-align: center;
  background-color: hsl(244deg, 38%, 16%);
  border-radius: 10rem;
  margin: 100rem 0;
}
.card p {
  color: hsla(0deg, 0%, 100%, 0.75);
}
.card h1,
.card h2 {
  color: hsl(0deg, 0%, 100%);
}
@media (min-width: 992px) {
  .card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20rem;
    max-width: 1200rem;
    text-align: left;
    margin: 0;
  }
}

.card-img {
  position: relative;
  background-color: hsl(277deg, 64%, 61%);
  border-top-left-radius: 10rem;
  border-top-right-radius: 10rem;
}
@media (min-width: 992px) {
  .card-img {
    border-top-left-radius: 0rem;
    border-bottom-right-radius: 10rem;
  }
}
.card-img img {
  border-top-left-radius: 10rem;
  border-top-right-radius: 10rem;
  mix-blend-mode: multiply;
  opacity: 70%;
}
@media (min-width: 992px) {
  .card-img img {
    border-top-left-radius: 0rem;
    border-bottom-right-radius: 10rem;
    border-top-right-radius: 10rem;
  }
}
@media (min-width: 480px) {
  .card-img {
    order: 2;
    width: 100%;
  }
}

.card-info {
  padding: 20rem;
}
.card-info h1 {
  font-size: 32rem;
}
.card-info span {
  color: hsl(277deg, 64%, 61%);
}
.card-info p {
  font-size: 18rem;
  line-height: 1.7;
}
@media (min-width: 480px) {
  .card-info h1 {
    font-size: 35rem;
  }
  .card-info p {
    font-size: 20rem;
  }
}
@media (min-width: 992px) {
  .card-info {
    padding: 70rem;
  }
  .card-info p {
    font-size: 18rem;
  }
}

.card-info-data {
  margin-top: 50rem;
}
@media (min-width: 992px) {
  .card-info-data {
    display: flex;
    justify-content: space-around;
    margin-top: 70rem;
  }
}

.data {
  text-align: center;
  margin-bottom: 30rem;
}
.data p {
  color: hsla(0deg, 0%, 100%, 0.6);
  text-transform: uppercase;
  font-family: "Lexend Deca", sans-serif;
}
@media (min-width: 992px) {
  .data {
    text-align: left;
    margin-bottom: 0;
  }
  .data h2 {
    font-size: 25rem;
  }
  .data p {
    font-size: 14rem;
  }
}